import React from "react";
import { Route, Switch } from "react-router-dom";
import SignupPage from "../authorization/SignupPage";
import SigninPage from "../authorization/SigninPage";
import Home from "../../Pages/Home/Home";
import UserProfile from "../../Pages/UserProfile/UserProfile";
import DataId from "../../Data/DataId";
import GalleryDetail from "../Gallery/GalleryId/GalleryDetail";
import ErrorFallback from "../DopComponents/ErrorFallback";
import Gallery from '../../Pages/GalleryPage/Gallery'


function Navigate() {
  return (
    <div>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/registration">
          <SignupPage />
        </Route>
        <Route path="/login">
           <SigninPage/>
        </Route>
        <Route path="/admin" exact>
          <UserProfile />
        </Route>
        <Route path="/notes/all" exact>
          <Gallery />
        </Route>
        <Route path="/data/:id">
          <DataId />
        </Route>
        <Route path="/gallery/:id">
          <GalleryDetail />
        </Route>
        <Route path="/other">
           <ErrorFallback/>
        </Route>
      </Switch>
    </div>
  );
}

export default Navigate;
