import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  deleteGallery,
  likeMeme,
  loadGallery,
} from '../../redux/features/gallery/gallery';
import GalleryHeader from '../../components/Gallery/Gallery/GalleryHeader';
import GalleryCardList from '../../components/Gallery/Gallery/GalleryCardList';
import GalleryInputForm from '../../components/Gallery/Gallery/GalleryInputForm';


function Gallery() {
  const [open, setOpen] = useState(false);
  const [sortOption, setSortOption] = useState('');
  const gallery = useSelector((state) => state.gallery.items);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadGallery(sortOption));
  }, [dispatch, sortOption]);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleLike = (id) => {
    dispatch(likeMeme(id));
  };

  const handleSort = (event) => {
    setSortOption(event.target.value);
  };

  const handleDelete = (id) => {
    dispatch(deleteGallery(id));
  };
  

  const sortedGallery = [...gallery].sort((a, b) => {
    if (sortOption === 'name') {
      return a.name.localeCompare(b.name);
    }
    if (sortOption === 'date') {
      return new Date(b.createdAt) - new Date(a.createdAt);
    }
    if (sortOption === 'description') {
      return a.description.localeCompare(b.description);
    }
    return 0;
  });

  sortedGallery.reverse();

  return (
    <>
      <GalleryHeader
        sortOption={sortOption}
        handleSort={handleSort}
        handleClickOpen={handleClickOpen}
      />
      <GalleryCardList
        gallery={sortedGallery}
        handleLike={handleLike}
        handleDelete={handleDelete}
      />
      <GalleryInputForm open={open} setOpen={setOpen} />
    </>
  );
}

export default Gallery;
